import React from 'react';


export default function Actions(cellData) {
  let data = cellData.data;
  
  const onEdit = () => {
    let blName = cellData.column.blName;
    let url = blName + '/edit/' + data.rowid;
    window.SDKDotNetHelper.invokeMethodAsync("NavigateTo", url);
  }

  const onDetail = () => {
    let blName = cellData.column.blName;
    let url = blName + '/detail/' + data.rowid;
    window.SDKDotNetHelper.invokeMethodAsync("NavigateTo", url);
  }
  
  return (

    <div class="action-buttons d-flex justify-content-center">
      <button class="btn btn-sm layout_btn_standar-sm ml-1" onClick={onEdit}><i class="fa-solid fa-pen"></i></button>
      <button class="btn btn-sm layout_btn_standar-sm ml-1"><i class="fa-solid fa-trash-can"></i></button>
      <button class="btn btn-sm layout_btn_standar-sm ml-1" onClick={onDetail}><i class="fa-solid fa-eye"></i></button>      
    </div>
  );
}

const mapStateToProps = state => {
  return {    
    'bl_name_list': state.general.bl_name_list,
  };
};